<template>
  <div>
    <div class="container1 fl-ex fl-co ju-be">
      <div class="pie" ref="chart"></div>
      <div class="sign">
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#ffa238;"></div>
          <p>
            总申请数:
            <span>5000</span>人
          </p>
          <div style="background-color:#ff9ca4;"></div>
          <p>
            帮扶总数:
            <span>3000</span>人
          </p>
        </div>
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#7bb6ff;"></div>
          <p>
            困难帮扶总数:
            <span>200</span>人
          </p>
          <div style="background-color:#69c15b; margin-right:-0.2rem;"></div>
          <p style="margin-right:-0.45rem;">
            残疾帮扶总数:
            <span>300</span>人
          </p>
        </div>
        <div class="sign1 fl-ex al-it">
          <div style="background-color:#2ef3df;"></div>
          <p style="margin-left:0.9rem;">
            就业帮扶总数:
            <span>2000</span>人
          </p>
        </div>
      </div>
    </div>
  </div>
  <!--  -->
</template>

<script>
const echarts = require("echarts");
export default {
  data() {
    return {};
  },
  methods: {
    initCharts() {
      let myChart = echarts.init(this.$refs.chart);
      myChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        calculable: true,
        series: [
          {
            color: [
              "#7eb9ff", //蓝色
              "#67c259", //绿色
              "#fe9ea0", //粉色
              "#ffa336", //黄色
              "#2ef2e8" //青色
            ],
            type: "pie",
            radius: [30, 125],
            center: ["50%", "50%"],
            roseType: "radius",
            label: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            lableLine: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            data: [
              { value: 21, name: "困难帮扶总数200人" },
              { value: 25, name: "残疾帮扶总数300人" },
              { value: 30, name: "帮扶总数3000人" },
              { value: 40, name: "总申请数5000人" },
              { value: 33, name: "就业帮扶总数2000人" }
            ]
          }
        ]
      });
    }
  },
  mounted() {
    this.initCharts();
  }
};
</script>

<style scoped>
.container1 {
  width: 100%;
  height: 25.5rem;
}
.container1 .pie {
  width: 90%;
  height: 18rem;
  margin: 0 auto;
}
.container1 .sign {
  width: 84%;
  height: 5.4rem;
  margin: 0 auto;
}
.container1 .sign .sign1 {
  width: 100%;
  height: 1.8rem;
}
.container1 .sign .sign1 > div {
  width: 1.2rem;
  height: 0.5rem;
}
.container1 .sign .sign1 > p {
  font-size: 0.85rem;
  color: black;
  text-align: left;
}
/* */
</style>


